<template>
    <div class="gutter--lg">
        <div class="row my-3">
            <va-card
                class="flex xs12 lg8 offset--xs0 offset--lg2 align-content--center"
                style="
                padding: 4px;
                "
            >
                <va-card color="success" gradient style="padding:1rem">
                    <h3 class="display-3 mb-4">使用者資料 ＆ 密碼修改</h3>
                    <va-card-title>姓名</va-card-title>
                    <va-card-content>{{ UserInfo.name }}</va-card-content>
                    <va-card-title>中心</va-card-title>
                    <va-card-content>{{ UserInfo.center }}</va-card-content>
                    <va-card-title>學校</va-card-title>
                    <va-card-content>{{ UserInfo.school }}</va-card-content>
                    <va-card-title>年級</va-card-title>
                    <va-card-content>{{ UserInfo.grade }}</va-card-content>
                    <va-card-content>
                        <va-form ref="password">
                            修改密碼：
                            <va-input
                                class="mb-4"
                                type="password"
                                v-model="currentStudentPassword"
                                :rules="[
                                    value => (value && value.length > 5 || value.length === 0) || '密碼長度不得小於6個字元',
                                    value => (value && value === confirmStudentPassword || value.length === 0) || '確認密碼與密碼不符',
                                ]"
                            />確認密碼：
                            <va-input
                                @keyup="$refs['password'].validate()"
                                class="mb-4"
                                type="password"
                                v-model="confirmStudentPassword"
                            />
                            <va-button color="warning" @click="updateUserLocal()">更新密碼</va-button>
                            <va-button color="secondary" @click="goBack()">回主畫面</va-button>
                        </va-form>
                    </va-card-content>
                </va-card>
            </va-card>
        </div>
    </div>
</template>
<script setup>
/* eslint-disable no-unused-vars */
import { useGameLogics } from "../compositions/useGameLogics";
import { ref } from "vue";
import { router } from "../router";

const goBack = () =>{
    router.push("/");
}

const password = ref(null);
const { updatePlayer, UserInfo } = useGameLogics();

const currentStudentPassword = ref('');
const confirmStudentPassword = ref('');

const updateUserLocal = () => {
    let update_info;
    if (currentStudentPassword.value !== null
        && currentStudentPassword.value.length > 0
    ) {
        if (currentStudentPassword.value !== confirmStudentPassword.value) {
            window.alert('確認密碼與密碼不符!!');
            return;
        }
        update_info = {
            password: currentStudentPassword.value,
        };
    } else {
        window.alert('沒有輸入密碼!!');
        return;
    }
    updatePlayer(update_info);
    currentStudentPassword.value="";
    confirmStudentPassword.value="";
    goBack();
}
</script>

<style scoped>
.clickable {
    cursor: pointer;
}

.clickable:hover {
    color: #003cff !important;
}

.display-7 {
    font-size: 1rem;
}
</style>