<template>
    <div class="layout gutter--sm" v-if="!gameEndChecker" @click="focusBack()">
        <template v-if="currentQUizs.length > 0">
            <div @click.prevent="focusBack(idx)" v-for="(quiz, idx) in currentQUizs" :key="'quiz' + idx" class="row"
                style="padding: 4px">
                <template v-if="idx < currentFocus + 10 && idx > currentFocus - 10">
                    <div class="flex xs12 lg8 offset--lg2">
                        <va-button block class="mr-2" color="secondary" :outline="QuizsAnswerStatus[idx].checker === null"
                            style="padding: 5px" :rounded="false"
                            @click="playAudioFromDb(spriteMap.get(quiz.answer.toUpperCase()))">
                            <span :class="!isMobile ? 'question_big mx-3' : 'question mx-3'">
                                {{ QuizsAnswerStatus[idx].checker !== null ? "已作答" : "" }}
                                {{ idx + 1 }}.
                                {{ quiz.quiz }}
                            </span>
                            <span :class="!isMobile ? 'question_big mx-3 code' : 'question mx-3 code'
                                ">
                                {{
                                    quiz.answer
                                        .split("")
                                        .map((e, idx) => {
                                            if (
                                                idx !== 0 &&
                                                (quiz.answer.length > 3
                                                    ? idx !== quiz.answer.length - 1
                                                    : true)
                                            ) {
                                                switch (e) {
                                                    case " ":
                                                        return " ";
                                                    case "-":
                                                        return "-";
                                                    case ".":
                                                        return ".";
                                                    case "'":
                                                        return "'";
                                                    default:
                                                        return "◻︎";
                                                }
                                            } else {
                                                return e;
                                            }
                                        })
                                        .join("")
                                }}
                            </span>
                            <va-icon size="large" name="record_voice_over" />
                        </va-button>
                    </div>
                    <div class="flex xs6 lg4 offset--lg2">
                        <input class="form_input" :type="isMobile ? 'password' : 'text'" autocomplete="new-password"
                            spellcheck="false" inputmode="verbatim" capitalize="off" autocorrect="off"
                            v-model="QuizsAnswerStatus[idx].ans" :readonly="!isIphone"
                            @input="(event) => onInput(event, idx)" @focus="(event) => {
                                !isIphone ? event.target.removeAttribute('readonly') : null;
                                inputFocused(idx);
                            }
                                " @blur="(event) => {
        !isIphone
            ? event.target.setAttribute('readonly', 'readonly')
            : null;
    }
        " @keyup.enter="enterSubmit(idx, QuizsAnswerStatus[idx].ans)" @keyup.up="focus('up', idx)"
                            @keyup.down="focus('down', idx)" @keydown.tab.prevent="
                                playAudioFromDb(spriteMap.get(quiz.answer.toUpperCase()))
                                " :ref="setQuizRef" />
                        <InputSpell :answer="quiz.answer" v-model="QuizsAnswerStatus[idx].ans" :ref="setDisplayRef" />
                    </div>
                    <div class="flex xs6 lg4 mb-8">
                        <va-button color="secondary" block :disabled="!ConnectionChecker" :loading="!ConnectionChecker"
                            @click.prevent="
                                enterSubmit(idx, QuizsAnswerStatus[idx].ans, true)
                                ">送出答案</va-button>
                    </div>
                    <div class="flex xs12" :ref="setFocusRef" :data-idx="idx">
                        <va-divider />
                    </div>
                </template>
            </div>
        </template>
    </div>
    <div class="layout gutter--sm" v-else>
        <div class="row" v-if="currentQUizs.length > 0" style="padding: 4px">
            <template v-for="(quiz, idx) in currentQUizs" :key="'quiz' + idx">
                <div class="flex xs12 lg8 offset--lg2">
                    <va-button block class="mr-2" :color="QuizsAnswerStatus[idx]?.checker !== null
                        ? QuizsAnswerStatus[idx].checker
                            ? 'success'
                            : 'danger'
                        : 'secondary'
                        " :outline="QuizsAnswerStatus[idx].checker === null" style="padding: 5px" :rounded="false"
                        @click="playAudioFromDb(spriteMap.get(quiz.answer.toUpperCase()))">
                        <span :class="isMobile ? 'question_big mx-3' : 'question mx-3'">
                            {{
                                QuizsAnswerStatus[idx].checker !== null
                                ? QuizsAnswerStatus[idx].checker
                                    ? "正確"
                                    : "錯誤"
                                : "沒作答"
                            }}
                            {{ idx + 1 }}.
                            {{ quiz.quiz }}
                        </span>
                        <va-icon size="large" name="record_voice_over" />
                    </va-button>
                </div>
                <div class="flex xs6 lg4 offset--lg2">
                    你的答案：{{ QuizsAnswerStatus[idx].ans }}
                </div>
                <div class="flex xs6 lg4 mb-8">
                    正確答案：{{ QuizsAnswerStatus[idx].correct_ans }}
                </div>
                <div class="flex xs12">
                    <va-divider />
                </div>
            </template>
        </div>
    </div>
</template>

<script setup>
import {
    ref,
    onUpdated,
    onBeforeUpdate,
    onMounted,
    watch,
    getCurrentInstance,
} from "vue";
import { useGameLogics } from "../compositions/useGameLogics";
import { useWSConnection } from "../compositions/useWSConnection";
// import { useHowl } from "../compositions/useHolwer";
import { useLoading } from "../compositions/useLoading";
import { getMobileOS } from "../lib/Utils";
import MobileChecker from "../lib/MobileChecker";
import InputSpell from "../components/InputSpell.vue";
import useVoiceDB from "../compositions/useVoiceDB";
const { playAudioFromDb, spriteMap } = useVoiceDB();
const isMobile = MobileChecker();
const isIphone = getMobileOS() === "iOS";
const instance = getCurrentInstance();
const { loading } = useLoading();
// const { howl,spriteMap } = useHowl();
const { ConnectionChecker } = useWSConnection();
const { currentQUizs, SubmitAns, QuizsAnswerStatus, currentRecord } =
    useGameLogics();

const quizsRefs = [];
const focusRefs = [];
const displayRefs = [];
let forceUpdate = false;
const currentFocus = ref(
    QuizsAnswerStatus.value.findIndex((e) => e.checker === null)
);

const findRefIdx = () => {
    let refIdx = focusRefs.findIndex(
        (e) => e?.dataset?.idx == currentFocus.value
    );
    return refIdx;
};

const inputFocused = (idx) => {
    // currentFocus.value = idx;
    const refIdx = findRefIdx();
    if (refIdx !== -1) {
        const position =
            QuizsAnswerStatus.value[idx].ans === null
                ? 0
                : QuizsAnswerStatus.value[idx].ans.length;
        displayRefs[refIdx].focus(position);
        instance.proxy?.$forceUpdate();
    }
};

const moveCursor = (e, idx) => {
    let cursorPosition = e.target.value.length;
    if (cursorPosition > currentQUizs.value[idx].answer.length) {
        e.target.selectionStart = e.target.selectionEnd = cursorPosition - 1;
        e.target.value = e.target.value.slice(0, -1);
        cursorPosition = e.target.selectionEnd;
    } else {
        e.target.selectionStart = e.target.selectionEnd = cursorPosition;
    }
    const refIdx = findRefIdx();
    if (refIdx !== -1) {
        displayRefs[refIdx].focus(cursorPosition);
    }
};
/**
 * @param {InputEvent} e
 */
const onInput = (e, idx) => {
    if (!isMobile) {
        moveCursor(e, idx);
        return;
    }
    const input = e.target;
    if (e.data === null || e.data === undefined) {
        if (e.inputType === "deleteContentBackward") {
            QuizsAnswerStatus.value[idx].ans = input.value;
        }
        moveCursor(e, idx);
        return;
    }
    if (e.inputType === "insertCompositionText" && e.data.length > 1) {
        input.value = input.value.slice(0, -(e.data.length + 1));
        input.value += e.data;
        QuizsAnswerStatus.value[idx].ans = input.value;
    } else if (
        e.inputType === "insertCompositionText" ||
        e.inputType === "insertText"
    ) {
        input.value = input.value.slice(0, -e.data.length);
        input.value += e.data;
        QuizsAnswerStatus.value[idx].ans = input.value;
    }
    moveCursor(e, idx);
    // check if input is mobile enter
    if (e.data === "\n" || e.keyCode === 13) {
        enterSubmit(idx, QuizsAnswerStatus[idx].ans);
    }
};
let clickAnswer = false;
const focusBack = (idx) => {
    if (clickAnswer) {
        clickAnswer = false;
        return;
    }
    forceUpdate = true;
    if (idx !== undefined) currentFocus.value = idx;
    instance?.proxy?.$forceUpdate();
};
watch(loading, (newVal, oldVal) => {
    if (newVal === false && oldVal === true) {
        forceUpdate = true;
        instance?.proxy?.$forceUpdate();
    }
});

const setDisplayRef = (el) => {
    displayRefs.push(el);
};
const setQuizRef = (el) => {
    quizsRefs.push(el);
};
const setFocusRef = (el) => {
    focusRefs.push(el);
};
onBeforeUpdate(() => {
    quizsRefs.splice(0, quizsRefs.length);
    focusRefs.splice(0, focusRefs.length);
    displayRefs.splice(0, displayRefs.length);
});

let preIdx = null;
onUpdated(() => {
    if (preIdx === currentFocus.value && !forceUpdate) {
        return;
    }
    forceUpdate = false;
    preIdx = currentFocus.value;
    let refIdx = findRefIdx();
    if (refIdx === -1) return;
    quizsRefs[refIdx]?.focus();
    moveCursor({ target: quizsRefs[refIdx] }, currentFocus.value);
    focusRefs[refIdx]?.scrollIntoView({
        behavior: "smooth",
        block: "center",
    });
    if (currentQUizs.value[currentFocus.value])
        playAudioFromDb(
            spriteMap.get(currentQUizs.value[currentFocus.value].answer.toUpperCase())
        );
});
onMounted(() => {
    if (!gameEndChecker.value) {
        let refIdx = findRefIdx();
        quizsRefs[refIdx].focus();
        focusRefs[refIdx].scrollIntoView({
            behavior: "smooth",
            block: "center",
        });
    }
});

const focus = (direction, idx) => {
    if (direction === "up") {
        currentFocus.value = idx - 1;
    } else {
        currentFocus.value = idx + 1;
    }
    instance?.proxy?.$forceUpdate();
};

const enterSubmit = (idx, ans, byclick = false) => {
    if (byclick) {
        clickAnswer = true;
    }
    if (ans !== null) {
        SubmitAns(idx, ans);
    }
    focus("down", idx);
};

const gameEndChecker = ref(
    currentRecord.value.game_result.game_ended ||
    new Date(currentRecord.value.game.time_limit) < new Date()
);
</script>
<style scoped>
.white {
    color: white;
}

.code {
    font-family: monospace;
}

.question {
    font-size: 1rem;
    padding: 2px;
    line-height: normal;
}

.question_big {
    font-size: 2rem;
    padding: 2px;
    line-height: normal;
}

.form_input {
    opacity: 0;
    width: 0;
    height: 0;
}

.form_input_display {
    font-family: "Roboto", sans-serif;
    color: #333;
    font-size: 1rem;
    margin: auto;
    padding: 3px;
    border-radius: 0.2rem;
    background-color: rgb(255, 255, 255);
    width: 90%;
    display: block;
    transition: all 0.3s;
}
</style>
