
<template>
    <div class="row my-3">
        <va-card
            class="flex xs12 lg8 offset--lg2 align-content--center"
            style="
                padding: 1rem;
                "
        >
            <div class="row">
                <div class="flex lg5 xs12 mb-1">
                    <va-button-group :rounded="false" outline color="primary">
                        <va-button @click="currentLevel = '初級'" :outline="currentLevel !== '初級'">初級</va-button>
                        <va-button @click="currentLevel = '中級'" :outline="currentLevel !== '中級'">中級</va-button>
                        <va-button @click="currentLevel = '高級'" :outline="currentLevel !== '高級'">高級</va-button>
                    </va-button-group>
                </div>
                <div v-if="!WidthEnough" class="flex lg7 xs12">
                    <va-button-group :rounded="false" outline color="secondary" class="mb-1">
                        <va-button @click="currentType = '英翻中'" :outline="currentType !== '英翻中'">英翻中</va-button>
                        <va-button @click="currentType = '中翻英'" :outline="currentType !== '中翻英'">中翻英</va-button>
                    </va-button-group>
                    <va-button-group :rounded="false" outline color="secondary">
                        <va-button @click="currentType = '聽力'" :outline="currentType !== '聽力'">聽力</va-button>
                        <va-button @click="currentType = '拼字'" :outline="currentType !== '拼字'">拼字</va-button>
                    </va-button-group>
                </div>
                <div v-else class="flex lg6 xs12 align-content--center">
                    <va-button-group :rounded="false" outline color="secondary">
                        <va-button @click="currentType = '英翻中'" :outline="currentType !== '英翻中'">英翻中</va-button>
                        <va-button @click="currentType = '中翻英'" :outline="currentType !== '中翻英'">中翻英</va-button>
                        <va-button @click="currentType = '聽力'" :outline="currentType !== '聽力'">聽力</va-button>
                        <va-button @click="currentType = '拼字'" :outline="currentType !== '拼字'">拼字</va-button>
                    </va-button-group>
                </div>
                <div class="flex xs12">
                    <va-input v-if="listChecker" block v-model="searchGame" placeholder="搜尋檢測" />
                    <table
                        style="
                            width: 100%;
                            "
                        class="va-table va-table--striped va-table--hoverable"
                        v-if="listChecker"
                        :key="refreshKey"
                    >
                        <thead>
                            <tr>
                                <th>活動名稱</th>
                                <th>比賽項目</th>
                                <th>級別</th>
                                <th>答對題數</th>
                                <th>參賽期限</th>
                                <th>操作</th>
                            </tr>
                        </thead>
                        <tr v-for="(game,idx) in currentGames" :key="'game' + idx">
                            <td>{{ game[1].game_info.gameTitle }}</td>
                            <td>{{ gameTypeMap[game[1].game_info.gameType] }}</td>
                            <td>{{ gameLevelMap[game[1].game_info.gameLevel] }}</td>
                            <td>
                                {{
                                    game[1].participated ?
                                        game[1].record?.game_result.game_ended ? game[1].record.game_result.current_score : '比賽尚未完成' :
                                        ''
                                }}
                            </td>
                            <td>{{ new Date(game[1].time_limit).toLocaleString("zh") }}</td>
                            <td>
                                <va-button
                                    v-if="game[1].participated === false"
                                    color="primary"
                                    :disabled="!ConnectionChecker"
                                    :loading="!ConnectionChecker"
                                    @click="SubmitGame(game[0])"
                                >參加比賽</va-button>
                                <va-button
                                    v-else-if="!game[1].record.game_result.game_ended"
                                    color="success"
                                    :disabled="!ConnectionChecker"
                                    :loading="!ConnectionChecker"
                                    @click="getQuizReal(game[0])"
                                >{{ game[1].record.game_result.ansed_quiz.length > 0 ? '繼續比賽' : '開始比賽' }}</va-button>
                                <va-button
                                    v-else
                                    color="dark"
                                    :disabled="!ConnectionChecker"
                                    :loading="!ConnectionChecker"
                                    @click="getQuizReal(game[0])"
                                >檢視比賽結果</va-button>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
        </va-card>
    </div>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { inject, ref, watch } from "vue";
import { useGameLogics } from "../compositions/useGameLogics";
import { useWSConnection } from "../compositions/useWSConnection.js"
import MobileChecker from "../lib/MobileChecker";
const WidthEnough = !MobileChecker();

const { ConnectionChecker } = useWSConnection();

const currentPage = inject("currentPage");
const {
    SubmitGame,
    GetQuizs,
    gameLists,
    messageQueue,
    gameTypeMap,
    gameLevelMap,
    currentLevel,
    currentType,
} = useGameLogics();

const listChecker = ref(false);
const refreshKey = ref(new Date().getTime() + 'GameList');
watch(() => [gameLists.value.length, messageQueue.size], (newVal) => {
    if (newVal[1] == 0) {
        currentGames.value = [...gameLists.value];
        listChecker.value = true;
    }
    refreshKey.value = new Date().getTime() + 'GameList';
    refreshList([searchGame.value])
})
const currentGames = ref([]);
const searchGame = ref("");

watch(() => [searchGame.value, messageQueue.size, currentLevel.value, currentType.value], (val)=>refreshList(val));

function refreshList(val) {
    currentGames.value = [...gameLists.value];

    // filter game level first
    currentGames.value = currentGames.value.filter(game =>
        gameLevelMap.value[game[1].game_info.gameLevel].includes(currentLevel.value)
    );
    // filter game type
    currentGames.value = currentGames.value.filter(game =>
        gameTypeMap.value[game[1].game_info.gameType].includes(currentType.value)
    );
    const [search] = val;
    if (search !== null && search.length > 0) {
        let searchArray = search.split(" ");

        // filter search words
        searchArray.forEach(searchWord => {
            currentGames.value = currentGames.value.filter(game =>
                game[1].game_info.gameTitle.includes(searchWord) ||
                game[1].time_limit.toLocaleString('zh').includes(searchWord) ||
                game[0].toString().includes(searchWord)
            );
        })
    }
    currentGames.value.sort((a, b) => {
        return a[1].game_info.gameTitle.localeCompare(b[1].game_info.gameTitle);
    });
}

const getQuizReal = (record_id) => {
    GetQuizs(record_id);
    currentPage.value = "GamePlay";
}

</script>

