
<template>
    <div class="row">
        <va-card
            class="flex xs12 sm8 md6 lg4 offset--xs0 offset--sm2 offset--md3 offset--lg4"
            style="padding: 1em"
        >
            <h3
                class="display-3"
                color="danger"
                v-if="disConnectMessage !== null"
            >{{ disConnectMessage }}</h3>
            <va-form ref="form" autocomplete="off">
                <label for="username">帳號：</label>
                <va-input
                    id="username"
                    class="mb-4"
                    v-model="username"
                    autocomplete="off"
                    :rules="[value => (value && value.length > 0) || '必須有帳號名稱']"
                />
                <label for="password">密碼：</label>
                <va-input
                    id="password"
                    class="mb-4"
                    v-model="password"
                    autocomplete="off"
                    type="password"
                    :rules="[value => (value && value.length > 0) || '必須輸入密碼']"
                    @keyup.enter="LoginAccount" 
                />
                <va-button @click="LoginAccount">登入</va-button>
            </va-form>
        </va-card>
    </div>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { inject, ref } from "vue";
import { useLoading } from "../lib/vue-loading-esm";
import { useGameLogics } from "../compositions/useGameLogics";
import { useWSConnection } from "../compositions/useWSConnection";

const currentPage = inject("currentPage");

const {
    Login,
} = useGameLogics();
const { disConnectMessage } = useWSConnection();

// const { loading } = useLoading();
const { VueLoading } = useLoading();
const form = ref(null);
const username = ref("");
const password = ref("");
const LoginAccount = async () => {
    if (form.value.validate()) {
        VueLoading(true);
        await Login(username.value, password.value);
        VueLoading(false);
    } else {
        window.alert("資料填寫不完全！")
        VueLoading(false);
    }
};


</script>

