import { router } from "../router";

export function doGetCaretPosition(oField) {

    // Initialize
    var iCaretPos = 0;

    // IE Support
    if (document.selection) {

        // Set focus on the element
        oField.focus();

        // To get cursor position, get empty selection range
        var oSel = document.selection.createRange();

        // Move selection start to 0 position
        oSel.moveStart('character', -oField.value.length);

        // The caret position is selection length
        iCaretPos = oSel.text.length;
    }

    // Firefox support
    else if (oField.selectionStart || oField.selectionStart == '0')
        iCaretPos = oField.selectionDirection == 'backward' ? oField.selectionStart : oField.selectionEnd;

    // Return results
    return iCaretPos;
}

export function getMobileOS() {
    const ua = navigator.userAgent;
    if (/android/i.test(ua)) {
        return "Android";
    } else if (
        /iPad|iPhone|iPod/.test(ua) ||
        (navigator.platform === "MacIntel" && navigator.maxTouchPoints > 1)
    ) {
        return "iOS";
    }
    return "Other";
}

export function DealingErrorRedirect(error) {
    // 錯誤處理
    let error_message =
        (error.response && error.response.data) ||
        error.message ||
        error.toString();
    if (typeof error_message === "object") {
        error_message = Object.keys(error_message).reduce((pre, cur) => {
            return pre + `${error_message[cur].toString()}  \n`;
        }, "");
    }
    router.push({
        name: "error",
        params: {
            pmessage: error_message,
        },
    });
}

export function openURLNew(url) {
    window.open(url, "_blank");
}
