<template>
    <div class="input-spell" tabindex="1">
        <span v-for="(_, idx) in alphabet_array" :key="idx" :class="{
            'alphabet': true,
            'active': idx === cursor_idx,
        }">
            {{ alphabet_array[idx] }}
        </span>
    </div>
</template>
<script>
import { defineComponent, ref, computed } from "vue";
export default defineComponent({
    name: "InputSpell",
    props: {
        modelValue: {
            type: String || null,
            require: true,
        },
        answer: {
            type: String,
            require: true,
        },
    },
    setup(props) {
        // const answer_array = ref(props.answer.split(""));
        const alphabet_array = computed(() => {
            return new Array(props.answer.length).fill(0).map((_, idx) => {
                return props.modelValue!==null ? props.modelValue[idx] : " ";
            });
        });
        const cursor_idx = ref(null);
        const focus = (idx) => cursor_idx.value=idx;
        return {
            alphabet_array,
            cursor_idx,
            focus
        };
    },
    methods: {
    },
});
</script>
<style scoped>
.input-spell {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 10px;
}

.alphabet {
    border-width: 1px;
    border-color: black;
    border-style: ridge;
    display: flex;
    justify-content: center;
    font-family: "Courier New", Courier, monospace;
    font-weight: bold;
    margin: 0.5px;
    width: 1.5em;
    height: 1.5em;
}

.alphabet.active {
    background-color: #76ed36;
    border-width: 2px;
}
</style>
