import { createRouter, createWebHashHistory } from 'vue-router';
import TestPlay from "./views/TestPlay.vue";
import UserInfo from "./views/UserInfo.vue";
import Error from "./views/Error";
import { useWSConnection } from "./compositions/useWSConnection";
const { ConnectionChecker } = useWSConnection();

const routes = [
    { name: "TestPlay", path: '/', component: TestPlay, meta: { permission: "any", title: "線上檢測" }, props: true },
    { name: "UserSetting", path: '/UserSetting', component: UserInfo, meta: { permission: "any", title: "帳號設定" }, props: true },
    { name: "error", path: '/error', component: Error, meta: { permission: "any", title: "錯誤頁面" }, props: true },
]

export const router = createRouter({
    history: createWebHashHistory(),
    routes
});

router.beforeEach((to, from, next) => {
    console.log(to, from, ConnectionChecker);
    if (!ConnectionChecker.value && to.path !== "/") {
        next({
            name: 'TestPlay',
        });
    } else {
        next()
    }
});