
<template>
    <Login v-if="!ConnectionChecker" />
    <template v-else>
        <div v-if="currentPage === 'GameList'">
            <GameListMenu />
            <GameList />
        </div>
        <div v-if="currentPage === 'GamePlay'">
            <GamePlayMenu />
            <GamePlayBody v-if="currentQUizs.length !== 0" />
        </div>
    </template>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { ref, provide } from "vue";
import { useGameLogics } from "../compositions/useGameLogics";
import Login from "./Login.vue";
import GameList from "./GameList.vue"
import GameListMenu from "./GameListMenu.vue"
import GamePlayMenu from "./GamePlayMenu.vue"
import GamePlayBody from "./GamePlayBody.vue"
import { useWSConnection } from "../compositions/useWSConnection.js"

const currentPage = ref("GameList")
const { ConnectionChecker } = useWSConnection();

provide("currentPage", currentPage);

const {
    currentQUizs
} = useGameLogics();

</script>

