
<template>
    <va-affix :offset-top="dWidth ? 35 : 0">
        <va-card color="white" gradient square outlined class="layout gutter--sm">
            <div class="row" style="padding:4px;">
                <va-button
                    block
                    class="m-2"
                    color="info"
                    style="padding:5px;"
                    :rounded="false"
                    @click="goBack"
                >返回遊戲選取(暫停遊戲)</va-button>
                <va-button
                    v-if="!gameEndChecker"
                    block
                    class="m-2"
                    color="success"
                    style="padding:5px;"
                    :rounded="false"
                    @click="SubmitRecordEndLocal"
                >
                    送出考卷（結束遊戲）
                    <br />
                    {{ timeDisplay }}
                </va-button>
            </div>
        </va-card>
    </va-affix>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { inject, ref, onBeforeUnmount } from "vue";
import { useGameLogics } from "../compositions/useGameLogics";

const currentPage = inject("currentPage");

const { UserInfo, QuizsAnswerStatus, currentQUizs, SubmitRecordEnd, currentRecord, GetGames, GetPastGames, currentListFlag } = useGameLogics();

const gameEndChecker = ref(currentRecord.value.game_result.game_ended);

const SubmitRecordEndLocal = () => {
    SubmitRecordEnd();
    goBack();
}

const timeLeft = () => {
    if (gameEndChecker.value) return 0;
    if (UserInfo.playingGames !== undefined && UserInfo.playingGames.get(currentRecord.value.record_id) !== undefined) {
        if (UserInfo.playingGames.get(currentRecord.value.record_id).play_time <= 0) {
            return 0;
        }
        return UserInfo.playingGames.get(currentRecord.value.record_id).play_time;
    } return -1;
};

const timeDisplay = ref('');
const hourMSeconds = 60 * 60 * 1000;
const minuteMSeconds = 60 * 1000;

const timeCounter = setInterval(() => {
    const timeRaw = timeLeft();
    if (timeRaw > 0) {
        let minutes = Math.floor((timeRaw % hourMSeconds) / minuteMSeconds);
        let seconds = Math.floor((timeRaw % minuteMSeconds) / 1000);
        timeDisplay.value = `還有${minutes}分${seconds}秒`;
    } else if (timeRaw <= 0) {
        timeDisplay.value = '已結束';
    }
}, 1000);

onBeforeUnmount(() => {
    clearInterval(timeCounter);
});

const goBack = () => {
    currentQUizs.value.splice(0, currentQUizs.value.length);
    QuizsAnswerStatus.value.splice(0, QuizsAnswerStatus.value.length);
    currentListFlag.value === "Current" ? GetGames() : GetPastGames();
    currentPage.value = "GameList"
}
var width = (window.innerWidth > 0) ? window.innerWidth : screen.width;
const dWidth = ref(width >= 720);

</script>

<style scoped>
</style>
