<template>
  <div class="layout gutter--sm" v-if="!gameEndChecker">
    <div class="row" v-if="currentQUizs.length > 0" style="padding: 4px">
      <template v-for="(quiz, idx) in currentQUizs" :key="'quiz' + idx">
        <div :class="{
            flex:true, xs12:true, lg8:true, 'offset--lg2':true,
            'active': idx === currentFocus,
        }" :ref="setFocusRef">
          <va-button block class="mr-2" color="secondary" :outline="QuizsAnswerStatus[idx].checker === null"
            style="padding: 5px" :rounded="false" @click=" ()=>
            {
              if(currentFocus === idx) {
                playAudioFromDb(
                spriteMap.get(
                quiz.quiz.length > 1
                ? quiz.quiz[1].toUpperCase()
                : quiz.quiz[0].toUpperCase()
                )
                )
                return;
              }
              currentFocus = idx;
            }">
            <span class="display-3 mx-3">
              {{ QuizsAnswerStatus[idx].checker !== null ? "已作答" : "" }}
              {{ idx + 1 }}.
              <span :class="widx % 2 === 1 ? 'underline' : ''" v-for="(word, widx) in quiz.quiz"
                :key="'quiz' + idx + 'w' + widx">{{ widx % 2 === 1 ? "[" : "" }}{{ word
                }}{{ widx % 2 === 1 ? "]" : "" }}</span>
            </span>
            <va-icon size="large" name="record_voice_over" />
          </va-button>
        </div>
        <div class="flex xs3 lg2 offset--lg2">
          <va-button block color="secondary" :outline="QuizsAnswerStatus[idx].ans !== 'a'"
            :disabled="!ConnectionChecker" :loading="!ConnectionChecker" @click="SubmitAnsLocal(idx, 'a')">
            {{ quiz.option1 }}</va-button>
        </div>
        <div class="flex xs3 lg2">
          <va-button color="secondary" :outline="QuizsAnswerStatus[idx].ans !== 'b'" block
            :disabled="!ConnectionChecker" :loading="!ConnectionChecker" @click="SubmitAnsLocal(idx, 'b')">
            {{ quiz.option2 }}</va-button>
        </div>
        <div class="flex xs3 lg2">
          <va-button color="secondary" :outline="QuizsAnswerStatus[idx].ans !== 'c'" block
            :disabled="!ConnectionChecker" :loading="!ConnectionChecker" @click="SubmitAnsLocal(idx, 'c')">
            {{ quiz.option3 }}</va-button>
        </div>
        <div class="flex xs3 lg2 mb-8">
          <va-button color="secondary" :outline="QuizsAnswerStatus[idx].ans !== 'd'" block
            :disabled="!ConnectionChecker" :loading="!ConnectionChecker" @click="SubmitAnsLocal(idx, 'd')">
            {{ quiz.option4 }}</va-button>
        </div>
        <div class="flex xs12">
          <va-divider />
        </div>
      </template>
    </div>
  </div>
  <div class="layout gutter--sm" v-else>
    <div class="row" v-if="currentQUizs.length > 0" style="padding: 4px">
      <template v-for="(quiz, idx) in currentQUizs" :key="'quiz' + idx">
        <div class="flex xs12 lg8 offset--lg2" :ref="setFocusRef">
          <va-button block class="mr-2" :color="
            QuizsAnswerStatus[idx]?.checker !== null
              ? QuizsAnswerStatus[idx].checker
                ? 'success'
                : 'danger'
              : 'secondary'
          " :outline="QuizsAnswerStatus[idx].checker === null" style="padding: 5px" :rounded="false" @click="
            playAudioFromDb(
              spriteMap.get(
                quiz.quiz.length > 1
                  ? quiz.quiz[1].toUpperCase()
                  : quiz.quiz[0].toUpperCase()
              )
            )
          ">
            <span class="display-3 mx-3">
              {{
              QuizsAnswerStatus[idx].checker !== null
              ? QuizsAnswerStatus[idx].checker
              ? "正確"
              : "錯誤"
              : "沒作答"
              }}
              {{ idx + 1 }}.
              <span class="display-3 mx-3">
                {{ QuizsAnswerStatus[idx].checker !== null ? "已作答" : "" }}
                {{ idx + 1 }}.
                <span :class="widx % 2 === 1 ? 'underline' : ''" v-for="(word, widx) in quiz.quiz"
                  :key="'quiz' + idx + 'w' + widx">{{ widx % 2 === 1 ? "[" : "" }}{{ word
                  }}{{ widx % 2 === 1 ? "]" : "" }}</span>
              </span>
            </span>
            <va-icon size="large" name="record_voice_over" />
          </va-button>
        </div>
        <div class="flex xs3 lg2 offset--lg2">
          <va-button block :color="
            QuizsAnswerStatus[idx].correct_ans !== 'A'
              ? QuizsAnswerStatus[idx].checker !== null
                ? QuizsAnswerStatus[idx].checker
                  ? 'success'
                  : 'danger'
                : 'secondary'
              : 'success'
          " :outline="
            QuizsAnswerStatus[idx].ans !== 'a' &&
            QuizsAnswerStatus[idx].correct_ans !== 'A'
          " :disabled="!ConnectionChecker" :loading="!ConnectionChecker">{{
          QuizsAnswerStatus[idx].correct_ans ===
          "A" ? "V正確答案：" : ""
          }}{{ quiz.option1 }}</va-button>
        </div>
        <div class="flex xs3 lg2">
          <va-button :color="
            QuizsAnswerStatus[idx].correct_ans !== 'B'
              ? QuizsAnswerStatus[idx].checker !== null
                ? QuizsAnswerStatus[idx].checker
                  ? 'success'
                  : 'danger'
                : 'secondary'
              : 'success'
          " :outline="
            QuizsAnswerStatus[idx].ans !== 'b' &&
            QuizsAnswerStatus[idx].correct_ans !== 'B'
          " block :disabled="!ConnectionChecker" :loading="!ConnectionChecker">{{
          QuizsAnswerStatus[idx].correct_ans
          === "B" ? "V正確答案：" : ""
          }}{{ quiz.option2 }}</va-button>
        </div>
        <div class="flex xs3 lg2">
          <va-button :color="
            QuizsAnswerStatus[idx].correct_ans !== 'C'
              ? QuizsAnswerStatus[idx].checker !== null
                ? QuizsAnswerStatus[idx].checker
                  ? 'success'
                  : 'danger'
                : 'secondary'
              : 'success'
          " :outline="
            QuizsAnswerStatus[idx].ans !== 'c' &&
            QuizsAnswerStatus[idx].correct_ans !== 'C'
          " block :disabled="!ConnectionChecker" :loading="!ConnectionChecker">{{
          QuizsAnswerStatus[idx].correct_ans
          === "C" ? "V正確答案：" : ""
          }}{{ quiz.option3 }}</va-button>
        </div>
        <div class="flex xs3 lg2 mb-8">
          <va-button :color="
            QuizsAnswerStatus[idx].correct_ans !== 'D'
              ? QuizsAnswerStatus[idx].checker !== null
                ? QuizsAnswerStatus[idx].checker
                  ? 'success'
                  : 'danger'
                : 'secondary'
              : 'success'
          " :outline="
            QuizsAnswerStatus[idx].ans !== 'd' &&
            QuizsAnswerStatus[idx].correct_ans !== 'D'
          " block :disabled="!ConnectionChecker" :loading="!ConnectionChecker">{{
          QuizsAnswerStatus[idx].correct_ans
          === "D" ? "V正確答案：" : ""
          }}{{ quiz.option4 }}</va-button>
        </div>
        <div class="flex xs12">
          <va-divider />
        </div>
      </template>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUpdated, onBeforeUnmount } from "vue";
import { useGameLogics } from "../compositions/useGameLogics";
import { useWSConnection } from "../compositions/useWSConnection";
import useVoiceDB from "../compositions/useVoiceDB";
// import { useHowl } from "../compositions/useHolwer";
// const { howl, spriteMap } = useHowl();
const { ConnectionChecker } = useWSConnection();
const { playAudioFromDb, spriteMap } = useVoiceDB();

const { currentQUizs, SubmitAns, QuizsAnswerStatus, currentRecord } =
  useGameLogics();

const gameEndChecker = ref(
  currentRecord.value.game_result.game_ended ||
  new Date(currentRecord.value.game.time_limit) < new Date()
);

const focusRefs = [];
const currentFocus = ref(
  QuizsAnswerStatus.value.findIndex((e) => e.checker === null)
);
const setFocusRef = (el) => {
  focusRefs.push(el);
};
const SubmitAnsLocal = (idx, ans) => {
  SubmitAns(idx, ans);
  currentFocus.value = idx + 1;
};
var preIdx = null;
onUpdated(() => {
  if (preIdx === currentFocus.value) {
    return;
  }
  preIdx = currentFocus.value;
  focusRefs[preIdx].scrollIntoView({
    behavior: "smooth",
    block: "center",
  });
  const quiz = currentQUizs.value[preIdx].quiz;
  playAudioFromDb(
    spriteMap.get(
      quiz.length > 1 ? quiz[1].toUpperCase() : quiz[0].toUpperCase()
    )
  );
});
onMounted(() => {
  if (!gameEndChecker.value && currentFocus.value !== -1) {
    focusRefs[currentFocus.value].scrollIntoView({
      behavior: "smooth",
      block: "center",
    });
    const quiz = currentQUizs.value[currentFocus.value]?.quiz;
    if (quiz) playAudioFromDb(
      spriteMap.get(
        quiz.length > 1 ? quiz[1].toUpperCase() : quiz[0].toUpperCase()
      )
    );
  } window.addEventListener("keydown", keyInput);
});

onBeforeUnmount(() => {
  window.removeEventListener("keydown", keyInput);
});
/**
 * @param {KeyboardEvent} e 
 */
const keyInput = (e) => {
  console.log(e.key)
  switch (e.key) {
    case 'a':
    case '1':
      SubmitAnsLocal(currentFocus.value, 'a');
      break;
    case 'b':
    case '2':
      SubmitAnsLocal(currentFocus.value, 'b');
      break;
    case 'c':
    case '3':
      SubmitAnsLocal(currentFocus.value, 'c');
      break;
    case 'd':
    case '4':
      SubmitAnsLocal(currentFocus.value, 'd');
      break;
    case "ArrowUp":
      currentFocus.value = currentFocus.value - 1 < 0 ? 0 : currentFocus.value - 1;
      e.stopPropagation();
      e.preventDefault();
      break;
    case "ArrowDown":
      currentFocus.value = currentFocus.value + 1 > currentQUizs.value.length - 1 ? currentQUizs.value.length - 1 : currentFocus.value + 1;
      e.stopPropagation();
      e.preventDefault();
      break;
    case " ":
    case "Tab":
    case "Enter": {
      e.stopPropagation();
      e.preventDefault();
      const quiz = currentQUizs.value[currentFocus.value]?.quiz;
      if (quiz) playAudioFromDb(
        spriteMap.get(
          quiz.length > 1 ? quiz[1].toUpperCase() : quiz[0].toUpperCase()
        )
      );
      break;
    }
  }
};
</script>
<style scoped>
.white {
  color: white;
}

.underline {
  text-decoration: underline;
}

.active {
  background-color: #caf639;
}
</style>
