import BlobOP from "../lib/utils/BlobOP";
import JSZip from "jszip";
// import { Storage, Drivers } from "@ionic/storage";
import VoicesFilesToLoad from "../lib/BoyoVBs.json";
import { ref } from "vue";



let createObjectStore = function (dataBase) {
    // Create an objectStore
    console.log("Creating objectStore")
    dataBase.createObjectStore("vbs");
};

var indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB || window.OIndexedDB || window.msIndexedDB;

function open_db(name, version) {
    return new Promise((resolve, reject) => {
        const req = indexedDB.open(name, version);
        req.onerror = reject;
        req.onupgradeneeded = e => {
            createObjectStore(e.target.result);
        };
        req.onsuccess = e => resolve(e.target.result);
    });
}

function getData(name, dataBase) {
    return new Promise((resolve, reject) => {
        let transaction = dataBase.transaction(["vbs"]);
        let get = transaction.objectStore("vbs").get(name);
        get.onsuccess = e => resolve(e.target.result);
        get.onerror = reject;
    });
}

async function putInDbArrayBuffer(vb, data) {
    let checker = await getData(vb, db.value);
    if (checker) {
        return;
    }
    var transaction = db.value.transaction(["vbs"], "readwrite");
    // Put the blob into the dabase
    let put = transaction.objectStore("vbs").put(data, vb);
    return new Promise((resolve, reject) => {
        put.onerror = reject;
        put.onsuccess = e => resolve(e);
    });
}

const db = ref(null);


const spriteNames = VoicesFilesToLoad.map(e => e.vb);
const spriteMap = new Map();
spriteNames.forEach((name) => {
    const UpperCased = name.toUpperCase();
    const UpperCasedWithVariantApostrophe = UpperCased.replace(/'/g, "’");
    const UpperCasedWithVariantApostrophe2 = UpperCasedWithVariantApostrophe.replace(/’/g, "'");
    spriteMap.set(name, name);
    spriteMap.set(UpperCased, name);
    spriteMap.set(UpperCasedWithVariantApostrophe, name);
    spriteMap.set(UpperCasedWithVariantApostrophe2, name);
    spriteMap.set(name + "es", name);
    spriteMap.set(name + "s", name);
});

export default function useVoiceDB() {
    return {
        vbLoadPercentage,
        downloadingPercentage,
        loading_status,
        InitialVoiceDB,
        checkInstalled,
        loadVBFilesFromArchive,
        getDbCount,
        checkVBinDB,
        getAudioFromDb,
        playAudioFromDb,
        spriteMap,
        // storage
    }
}

