const vbLoadProgress = ref(0);
const vbLoadPercentage = ref(0);
const vbCurrentLoadingSize = ref(0);
const downloadingArchive = ref(null);
const downloadArchiveNumber = ref(3);
const downloadingPercentage = ref(null);
const loading_status = ref(false);
const loadedMp3 = new Map();
async function InitialVoiceDB() {
    try {
        db.value = await open_db("VBAppFiles", 1);
    } catch (error) {
        console.log("資料庫創建出錯！");
    }
}

async function checkInstalled() {
    let checker = true;
    try {
        for (let index = 0; index < VoicesFilesToLoad.length; index++) {
            const vb = VoicesFilesToLoad[index].vb;
            let currentChecker = await getData(vb.toLowerCase(), db.value);
            checker = checker && currentChecker;
            if (!checker) {
                console.log("未安裝", vb);

            }
            // Don't need to check evey words, just one absent is enough
            if (!checker) break;
        }
    } catch (error) {
        console.log(error);
        checker = false;
    }
    if (checker) vbLoadPercentage.value = 1;
    return checker;
}

function loadVBFilesFromArchive() {
    loading_status.value = true;
    return new Promise((resolve) => {
        for (let index = 1; index < downloadArchiveNumber.value + 1; index++) {
            storeArchive(index, resolve);
        }
    });
}

function getAudioArchive(number) {
    // Create XHR
    var xhr = new XMLHttpRequest();
    xhr.open("GET", `zip/test_${number}.zip`, true);
    // Set the responseType to blob
    xhr.responseType = "blob";
    xhr.send();
    downloadingArchive.value = number;
    return new Promise((resolve, reject) => {
        xhr.onprogress = (e) => {
            downloadingPercentage.value = e.loaded / e.total;
        };
        xhr.onerror = reject;
        xhr.onload = () => {
            if (xhr.status === 200) {
                resolve(xhr.response);
            } else {
                reject(xhr.status);
            }
        };
    });
}

async function storeArchive(number, resolve) {
    let data = await getAudioArchive(number);
    let zip = new JSZip();
    await zip.loadAsync(data);
    let files_names = Object.keys(zip.files);
    vbCurrentLoadingSize.value += files_names.length;
    for (let index = 0; index < files_names.length; index++) {
        const file_name = files_names[index];
        const vb_name = file_name.replaceAll(/\.mp3/g, "").toLowerCase();
        const audio_data = await zip.file(file_name)
            .async("arraybuffer");
        putInDbArrayBuffer(vb_name, audio_data).then(() => {
            vbLoadProgress.value++;
            vbLoadPercentage.value = vbLoadProgress.value / vbCurrentLoadingSize.value;
            if (vbLoadPercentage.value === 1 && downloadingArchive.value === downloadArchiveNumber.value) {
                loading_status.value = false;
                resolve(true);
            }
        });

    }
}

async function getDbCount() {
    return new Promise((resolve, reject) => {
        let countReq = db.value.transaction(["vbs"]).objectStore("vbs").count();
        countReq.onsuccess = e => resolve(e.target.result);
        countReq.onerror = reject;
    })
}

async function checkVBinDB(vb) {
    var data = await getData(vb, db.value);
    if (data instanceof ArrayBuffer) {
        return true;
    } else {
        return false;
    }
}

async function playAudioFromDb(vb) {
    /**
     * @type {HTMLAudioElement}
     */
    var audio = await getAudioFromDb(vb.toLowerCase());
    try {
        audio.play();
    } catch (err) {
        console.log(err)
        window.alert("發音尚未讀取完成");
    }
}

async function getAudioFromDb(vb) {
    if (loadedMp3.has(vb)) {
        return loadedMp3.get(vb);
    }
    // Retrieve the file that was just stored
    var data = await getData(vb.toLowerCase(), db.value);
    if (data === null) return false;

    // convert arraybuffer to blob
    var blob = BlobOP.arrayBufferToBlob(data, 'audio/mp3');

    // Get window.URL object
    var URL = window.URL || window.webkitURL;

    // Create and revoke ObjectURL
    var mp3URL = URL.createObjectURL(blob);
    // create HTML5 audio element
    var audio = new Audio(mp3URL);
    loadedMp3.set(vb, audio);
    return audio;
}
