import { ref, watch } from "vue";
const loading = ref(false);
const dialog = createLoading("讀取中");
export function useLoading() {

    watch(loading, (newVal) => {
        if (newVal) {
            showLoading();
        } else {
            disableLoading();
        }
    })
    return {
        loading
    }
}

let previousFocusElement = null;

function showLoading() {
    if (dialog.open) {
        return;
    }
    previousFocusElement = document.activeElement;
    dialog.showModal();
}

function disableLoading() {
    dialog.close();
    if (typeof previousFocusElement?.focus === "function") previousFocusElement?.focus();
    previousFocusElement = null;
}

function createLoading(message) {
    const dialog = document.createElement('dialog')
    // dialog.style.display = 'flex';
    dialog.style.width = "40%";
    dialog.style.height = "40%";
    dialog.style.textAlign = "center";
    const container = document.createElement('div');
    container.style.display = 'flex';
    container.style.flexDirection = 'row';
    container.style.alignItems = 'center';
    container.style.justifyContent = 'center';
    container.style.height = '100%';
    container.style.width = '100%';
    // const messageHead = document.createElement('h3')
    // messageHead.textContent = message;
    const messageArray = message.split("");
    messageArray.forEach((char, idx) => {
        const span = document.createElement("div");
        span.textContent = char;
        span.classList.add("loadingico");
        span.classList.add(`bouncing${idx % 3 + 1}`);
        container.appendChild(span);
    })
    // dialog.appendChild(messageHead)
    dialog.appendChild(container)
    document.body.appendChild(dialog)
    return dialog;
}