
<template>
    <E2CGame v-if="currentGame==='e2c'"/>
    <C2EGame v-if="currentGame==='c2e'"/>
    <ListeningGame v-if="currentGame==='listening'"/>
    <SpellGame v-if="currentGame==='spell'"/>
</template>

<script setup>
import { computed } from "vue";
import { useGameLogics } from "../compositions/useGameLogics";
import E2CGame from "./GamePlayE2C.vue";
import C2EGame from "./GamePlayC2E.vue";
import ListeningGame from "./GamePlayListening.vue";
import SpellGame from "./GamePlaySpell.vue";

const {
    currentRecord,
    gameLists,
} = useGameLogics();

const currentGame = computed(() => {
    const theGame = gameLists.value.find(
    (game) => game[0] === currentRecord.value.game_id
);
    if(theGame !== undefined){
        return theGame[1].game_info.gameType;
    }else{
        return "";
    }
});


</script>
<style scoped>
.white {
    color: white;
}
</style>
