<template>
  <NavBar />
  <div class="content">
    <router-view />
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { defineComponent, onMounted, onBeforeMount, watch } from "vue";
import NavBar from "./components/NavBar.vue";
import { useGameLogics } from "./compositions/useGameLogics";
import { useColors } from "vuestic-ui";
// import { useHowl } from "./compositions/useHolwer";
import useVoiceDB from "./compositions/useVoiceDB";
// import { useLoading } from "./compositions/useLoading";
import { useLoading } from "./lib/vue-loading-esm";
import { useVueWindow } from "./compositions/useWindow";
const { innerWidth } = useVueWindow();

export default defineComponent({
  name: "App",
  setup() {
    const {
      InitialVoiceDB,
      checkInstalled,
      loadVBFilesFromArchive,
      playAudioFromDb,
      getAudioFromDb,
      vbLoadPercentage,
      downloadingPercentage
    } = useVoiceDB();
    const { setColors } = useColors();
    const { messageQueue, LogStatusCheck } = useGameLogics();
    const { VueLoadingConfig, VueLoading } = useLoading();
    const loadingString = `
      <div class="w3-card">
            <header class="w3-container w3-center w3-blue w3-padding">
              <h3 class="w3-xxlarge">主機訊息傳遞中<i class="fa fa-spinner w3-spin"></i></h3>
            </header>
          <div class="w3-container w3-grey w3-xlarge" style="width:400px">
          </div>
          </div>
      `;
    VueLoadingConfig({
      bg: "#90909090",
      slot: loadingString,
    });
    watch(
      () => [messageQueue.size, vbLoadPercentage.value],
      (values) => {
        if (values[1] !== 1) {
          const downloadCheck = downloadingPercentage.value !== null && downloadingPercentage.value !== 1;
          const currentProgressinPercent = downloadCheck ?
            (downloadingPercentage.value * 100).toFixed(1) :
            (values[1] * 100).toFixed(1);
          VueLoadingConfig({
            bg: "#90909090",
            slot: `
          <div class="w3-card">
            <header class="w3-container w3-center w3-blue w3-padding">
              <h3 class="w3-xxlarge">${downloadCheck ? '單字發音下載中' : '單字發音安裝中'}</h3>
            </header>
          <div class="w3-container w3-grey w3-xlarge" style="width:400px">
            <div class="w3-container w3-green w3-center w3-padding" style="width:${currentProgressinPercent}%">${currentProgressinPercent}%</div>
          </div>
          </div>
          `,
          });
          VueLoading(true);
          return;
        }
        if (values[0] > 0) {
          VueLoadingConfig({
            bg: "#90909090",
            slot: loadingString,
          });
          VueLoading(true);
        } else {
          VueLoading(false);
          VueLoadingConfig({
            bg: "#90909090",
            slot: loadingString,
          });
        }
      }
    );
    onBeforeMount(async () => {
      setColors({
        primary: "#22bf0f",
      });
      innerWidth.value = window.innerWidth;
      await LogStatusCheck();
    });
    onMounted(async () => {
      let system_built_time =
        window.document.documentElement.dataset.buildTimestampUtc;
      let local_built_time = window.localStorage.getItem(
        "BoyoVBCompetitionBuiltTime"
      );
      if (local_built_time !== system_built_time) {
        window.localStorage.removeItem("BoyoVBCompetitionBuiltTime");
        window.localStorage.setItem(
          "BoyoVBCompetitionBuiltTime",
          system_built_time
        );
      }
    });
    InitialVoiceDB().then(() =>
      checkInstalled().then((checker) => {
        if (!checker) {
          console.log("installing");
          console.log(playAudioFromDb);
          console.log(getAudioFromDb);
          loadVBFilesFromArchive();
        }
      })
    );
    return;
  },
  components: { NavBar },
});
</script>

<style>
.content {
  padding: 1rem;
}
</style>
